<template>
  <v-container>
    <v-data-table
        :headers="headers"
        :items="dataRadar"
        :search="search"
        sort-by="Tipo"
        class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar
            flat
        >
          <v-col
              cols="8"
              sm="4"
              md="2"
          >
            <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Buscar"
                single-line
                hide-details
            ></v-text-field>
          </v-col>

          <v-spacer></v-spacer>
          <v-dialog
              v-model="dialog"
              max-width="900px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
              >
                Añadir
              </v-btn>
            </template>
            <validation-observer
                ref="observer"
                v-slot="{ invalid }"
            >
              <form @submit.prevent="submit">
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>

                    <v-row>
                      <v-col
                          cols="12"
                          sm="6"
                          md="4"
                      >
                        <validation-provider
                            v-slot="{ errors }"
                            name="MaintenanceDate"
                            rules="required|max:10"
                        >
                          <v-text-field
                              v-model="editedItem.maintenanceDate"
                              :error-messages="errors"
                              label="Fecha de Mantenimiento"
                              required
                          ></v-text-field>
                        </validation-provider>
                      </v-col>
                      <v-col
                          cols="12"
                          sm="6"
                          md="4"
                      >
                        <validation-provider
                            v-slot="{ errors }"
                            name="Mode"
                            rules="required|max:10"
                        >
                          <v-text-field
                              v-model="editedItem.mode"
                              :error-messages="errors"
                              label="Modo"
                              required
                          ></v-text-field>
                        </validation-provider>
                      </v-col>
                      <v-col
                          cols="12"
                          sm="6"
                          md="4"
                      >
                        <validation-provider
                            v-slot="{ errors }"
                            name="obscombus"
                            rules="required|max:10"
                        >
                          <v-text-field
                              v-model="editedItem.obscombus"
                              :error-messages="errors"
                              label="obscombus"
                              required
                          ></v-text-field>
                        </validation-provider>
                      </v-col>
                      <v-col
                          cols="12"
                          sm="6"
                          md="4"
                      >
                        <validation-provider
                            v-slot="{ errors }"
                            name="situation"
                            rules="required|max:10"
                        >
                          <v-text-field
                              v-model="editedItem.situation"
                              :error-messages="errors"
                              label="Situación"
                              required
                          ></v-text-field>
                        </validation-provider>
                      </v-col>
                      <v-col
                          cols="12"
                          sm="6"
                          md="4"
                      >
                        <validation-provider
                            v-slot="{ errors }"
                            name="NumParte"
                            rules="required|max:10"
                        >
                          <v-text-field
                              v-model="editedItem.numParte"
                              :error-messages="errors"
                              label="Número de partes"
                              required
                          ></v-text-field>
                        </validation-provider>
                      </v-col>

                      <v-col
                          cols="12"
                          sm="6"
                          md="4"
                      >
                        <validation-provider
                            v-slot="{ errors }"
                            name="select"
                            rules="required"
                        >
                          <v-select
                              v-model="select"
                              :items="dataSquadron"
                              :error-messages="errors"
                              label="Selecionar escuadrón"
                              item-text="description"
                              item-value="id"
                              data-vv-name="select"
                              bottom
                              autocomplete
                              required
                          ></v-select>
                        </validation-provider>
                      </v-col>
                      <v-col
                          cols="12"
                          sm="6"
                          md="4"
                      >
                        <validation-provider
                            v-slot="{ errors }"
                            name="selectLocation"
                            rules="required"
                        >
                          <v-select
                              v-model="selectLocation"
                              :items="dataLocation"
                              :error-messages="errors"
                              label="Selecionar ubicación"
                              item-text="location"
                              item-value="id"
                              data-vv-name="selectLocation"
                              bottom
                              autocomplete
                              required
                          ></v-select>
                        </validation-provider>
                      </v-col>
                      <v-col
                          cols="12"
                          sm="6"
                          md="4"
                      >
                        <validation-provider
                            v-slot="{ errors }"
                            name="selectRadarType"
                            rules="required"
                        >
                          <v-select
                              v-model="selectRadarType"
                              :items="dataRadarType"
                              :error-messages="errors"
                              label="Selecionar Tipo de radar"
                              item-text="description"
                              item-value="id"
                              data-vv-name="selectRadarType"
                              bottom
                              autocomplete
                              required
                          ></v-select>
                        </validation-provider>
                      </v-col>
                    </v-row>


                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                    color="blue darken-1"
                    text
                    @click="close"
                >
                  Cancelar
                </v-btn>
                <v-btn
                    class="mr-4"
                    type="submit"
                    color="#002F6C"
                    style="color: #E5F0F4;"
                    :disabled="invalid"
                >
                  Guardar
                </v-btn>
              </v-card-actions>
              <br/>

            </v-card>
              </form>
            </validation-observer>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">¿Está seguro que desea eliminar?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
            small
            @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
  </v-container>

</template>

<script>
import { required,  max,  } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'
setInteractionMode('eager')

extend('required', {
  ...required,
  message: ' Campo no puede ir vacío',
})

extend('max', {
  ...max,
  message: ' No puede ser mayor que {length} caracteres',
})



export default {
  name:'TableRadar',
  components: {
    ValidationProvider,
    ValidationObserver,
  },
  props: ['dataRadar', 'dataSquadron', 'dataLocation', 'dataRadarType' ],
  data: () => ({
    search: '',
    checkbox: null,
    dialog: false,
    dialogDelete: false,
    updateRadar: false,
    select: null,
    selectLocation: null,
    selectRadarType: null,
    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        value: 'id',
      },
      { text: 'mode', value: 'mode' },
      { text: 'obscombus', value: 'obscombus' },
      { text: 'numParte', value: 'numParte' },
      { text: 'Localización', value: 'location.location' },
      { text: 'type', value: 'type' },
      { text: 'situation', value: 'situation' },
      { text: 'Actions', value: 'actions', sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      createdAt: "",
      createdBy: "",
      dateFuel: "",
      factoryId: 0,
      fuel: 0,
      id: 0,
      location: {
        id: 0,
        location: null,
      },
      maintenanceDate: "",
      mode: "",
      numParte: "",
      obscombus: "",
      personalComboId: "",
      radar: "",
      radarType: {
        id: 0
      },
      situation: "",
      squadron: {
        id: 0
      },
      sureId: 0,
      timeRadarId: 0,
      type: 0,
      updatedAt: "",
      updatedBy: "",
      weather: 0
    },
    defaultItem: {
    },
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nuevo Radar' : 'Editar Radar'
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },

  methods: {
    submit () {
      if (this.$refs.observer.validate()) {
        this.editedItem.squadron.id = this.select
        this.editedItem.location.id = this.selectLocation
        this.editedItem.radarType.id = this.selectRadarType
        this.save()
      } else {
        console.log('completar los campos')
      }

    },

    editItem (item) {
      this.editedItem = Object.assign({}, item)
      this.select = this.editedItem.squadron.id
      this.selectLocation = this.editedItem.location.id
      this.selectRadarType = this.editedItem.radarType.id
      this.updateRadar = true;
      this.dialog = true
    },

    deleteItem (item) {
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.$emit('DeleteRadar', this.editedItem.id)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      if(this.updateRadar) {
        this.$emit('updateRadar', this.editedItem)
      } else {
        this.$emit('addRadar', this.editedItem)
      }

      this.updateRadar = false
      this.close()
    },
  },
}
</script>
<style scoped>

</style>