<template>
  <div>
    <div class="container">
      <h1>Radar</h1>
    </div>
    <table-radar-component
        @addRadar="addRadar"
        @updateRadar="updateRadar"
        @DeleteRadar="DeleteRadar"
        :dataRadar="radarListState"
        :dataSquadron="SquadronListState"
        :dataLocation="locationListState"
        :dataRadarType="radarTypeListState"
    />
  </div>

</template>

<script>
import tableRadarComponent from "@/components/inventaryManagement/radar/TableRadarComponent";
import {mapActions, mapGetters} from 'vuex'
export default {
  name: "RadarComponent",
  components: {
    tableRadarComponent
  },
  props: ['inventaryDataItem'],
  data () {
    return {
      dataRadar: [],
      jsonDataUpdate: {
        createdBy: "",
        dateFuel: "",
        factoryId: 0,
        fuel: 0,
        id: 0,
        location: {
          id: 1,
        },
        maintenanceDate: "",
        mode: "",
        numParte: "",
        obscombus: "",
        personalComboId: "",
        radar: "",
        radarType: {
          id: 0
        },
        situation: "",
        squadron: {
          id: 1
        },
        sureId: 0,
        timeRadarId: 0,
        type: 0,
        updatedBy: "",
        weather: 0

      }
    }
  },
  mounted() {
    this.getRadarState()
    this.getSquadronState()
    this.getLocationState()
    this.getRadarTypeState()
  },
  computed: {
    ...mapGetters(['radarListState', 'SquadronListState', 'locationListState', 'radarTypeListState'])
  },
  methods: {
    ...mapActions(['getRadarState','getRadarTypeState', 'getLocationState', 'setRadarStatenew', 'apiUpdateRadar', 'apiDeleteRadar','getSquadronState',]),

    async addRadar( item) {
      await this.setRadarStatenew(item)
      await  this.updateData()
    },
    async updateRadar( item) {
      this.jsonDataUpdate.dateFuel = item.dateFuel;
      this.jsonDataUpdate.createdBy = item.createdBy;
      this.jsonDataUpdate.factoryId = item.factoryId;
      this.jsonDataUpdate.fuel = item.fuel;
      this.jsonDataUpdate.id = item.id;
      this.jsonDataUpdate.maintenanceDate = item.maintenanceDate;
      this.jsonDataUpdate.mode = item.mode;
      this.jsonDataUpdate.numParte = item.numParte;
      this.jsonDataUpdate.obscombus = item.obscombus;
      this.jsonDataUpdate.personalComboId = item.personalComboId;
      this.jsonDataUpdate.radar = item.radar;
      this.jsonDataUpdate.radarType.id = item.radarType.id;

      this.jsonDataUpdate.situation = item.situation;
      this.jsonDataUpdate.squadron.id = item.squadron.id;
      this.jsonDataUpdate.sureId = item.sureId;
      this.jsonDataUpdate.timeRadarId = item.timeRadarId;
      this.jsonDataUpdate.type= item.type;
      this.jsonDataUpdate.updatedBy = item.updatedBy;
      this.jsonDataUpdate.weather = item.weather;
      this.apiUpdateRadar(this.jsonDataUpdate)
      this.updateData()
    },
    async updateData() {
      await this.getRadarState()
    },
    async DeleteRadar(item) {
      await this.apiDeleteRadar(item)
      await  this.updateData()
    }
  }
}
</script>

